@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.content {
  overflow-y: visible;
  margin-left: auto;
  margin-right: auto; }
  .content .paragraphLink {
    color: #FF00FF;
    font-weight: bold; }

body {
  padding: 0;
  font-family: "Raleway", sans-serif;
  overflow-x: hidden;
  color: #333333; }

h1 {
  text-align: center;
  margin-bottom: 1em; }

.page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 100%;
  height: 100%;
  overflow-x: hidden; }

.document {
  margin-top: 50px;
  margin-bottom: 50px;
  width: 562px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #333333;
  padding: 50px 90px;
  background-color: white; }
  @media screen and (max-width: 742px) {
    .document {
      margin-top: 30px;
      padding: 0px 30px;
      width: calc(100vw - 60px);
      border: none; } }

.title h1 {
  display: block;
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
  font-size: 2rem; }

.subtitle h2 {
  font-size: 1rem;
  font-weight: bold;
  margin: 0px; }

.paragraph p {
  font-size: 1rem;
  margin: 0px 0px 10px 0px; }

@media screen and (max-width: 742px) {
  .negative-margin {
    margin-top: -50px; } }

.background {
  z-index: -1;
  margin-left: -50%;
  width: 200%;
  height: 100%;
  position: absolute; }
  @media screen and (max-width: 742px) {
    .background {
      display: none; } }

.flex-row {
  display: flex;
  flex-direction: row; }

.flex-col {
  display: flex;
  flex-direction: column; }

@media screen and (max-width: 742px) {
  .hide-small, #KDSheader * {
    display: none !important; } }

@media screen and (min-width: 1350px) {
  .hide-large {
    display: none !important; } }

.show-small {
  display: none !important; }
  @media screen and (max-width: 742px) {
    .show-small {
      display: inherit !important; } }

.show-large {
  display: none !important; }
  @media screen and (min-width: 1350px) {
    .show-large {
      display: inherit !important; } }

.hide-schedule {
  display: none !important; }
  @media screen and (max-width: 1086px) {
    .hide-schedule {
      display: inherit !important; } }

.show-schedule {
  display: none !important; }
  @media screen and (min-width: 1086px) {
    .show-schedule {
      display: inherit !important; } }

.schedule .schedule-document {
  width: 1086px;
  margin-left: auto;
  margin-right: auto; }
  @media screen and (max-width: 1086px) {
    .schedule .schedule-document {
      width: 100vw; } }

.schedule .day-selector-top {
  cursor: pointer;
  position: absolute;
  top: 32px;
  left: 0px;
  width: 100%;
  text-align: center; }
  @media screen and (min-width: 742px) {
    .schedule .day-selector-top {
      display: none; } }
  .schedule .day-selector-top .header-day {
    font-size: 1rem; }
  .schedule .day-selector-top .header-day.selected {
    font-weight: bold; }

.schedule .slot:nth-child(odd) .event-header {
  background-color: #333333;
  color: #F1F0ED; }

.schedule .slot:nth-child(even) .event-header {
  background-color: #F1F0ED; }

.schedule .event-header {
  width: 100%;
  display: flex;
  justify-content: space-between; }
  .schedule .event-header .type {
    float: left;
    margin-left: 30px;
    padding: 10px;
    font-size: 1rem;
    font-weight: bold; }
  .schedule .event-header .time {
    float: right;
    margin-right: 30px;
    padding: 10px;
    font-size: 1rem;
    font-weight: bold; }

div.schedule {
  width: 100%;
  padding: 0;
  margin: 0; }
  div.schedule .schedule-container {
    background-color: white;
    margin-bottom: 90px;
    margin-top: 50px; }
    @media screen and (min-width: 1086px) {
      div.schedule .schedule-container {
        border: 1px solid #333333; } }
    @media screen and (min-width: 742px) {
      div.schedule .schedule-container {
        border-top: 1px solid #333333; } }
    div.schedule .schedule-container > .header {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px; }
      @media screen and (max-width: 742px) {
        div.schedule .schedule-container > .header {
          display: none; } }
      div.schedule .schedule-container > .header .schedule-filter {
        height: 0;
        float: left; }
      div.schedule .schedule-container > .header .header-title {
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        justify-items: center;
        align-items: center; }
      div.schedule .schedule-container > .header .title {
        margin-bottom: 16px; }
      div.schedule .schedule-container > .header .day-selector-header {
        word-spacing: 12px; }
        div.schedule .schedule-container > .header .day-selector-header .seperator {
          font-size: 1.5rem; }
        div.schedule .schedule-container > .header .day-selector-header .header-day {
          cursor: pointer;
          font-size: 1.125rem; }
        div.schedule .schedule-container > .header .day-selector-header .header-day.selected {
          font-weight: bold; }
