.pin-container {
  top: 0;
  left: 0; }

.pin {
  height: 30px;
  width: 22px;
  margin-left: auto;
  margin-right: auto; }
  .pin .top {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-radius: 50%;
    border: 8px solid #333333;
    width: 24px;
    height: 24px; }
  .pin .bottom {
    width: 0;
    height: 0;
    margin-top: -7px;
    margin-left: 1px;
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-top: 15px solid #333333; }

.pin.small {
  height: 20px;
  width: 16px;
  margin-left: auto;
  margin-right: auto; }
  .pin.small .top {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border-radius: 50%;
    border: 6px solid #333333;
    width: 18px;
    height: 18px; }
  .pin.small .bottom {
    width: 0;
    height: 0;
    margin-top: -5px;
    margin-left: 1px;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 12px solid #333333; }
