#favourite-talk-button {
  background-color: Transparent;
  border: none; }

.talk .header {
  height: 100%; }
  .talk .header .diff,
  .talk .header .room,
  .talk .header .time,
  .talk .header .heart {
    margin-top: auto;
    margin-bottom: auto; }
  .talk .header .diff .diff-name,
  .talk .header .room .room-name,
  .talk .header .time .time-text {
    display: block;
    font-size: 0.75rem;
    height: 100%; }

.talk-hidden {
  visibility: hidden; }
  @media only screen and (max-width: 1086px) {
    .talk-hidden {
      display: none !important; } }
